.card-form {
    width: 400px;
    height: 200px;
    background-color: lightgrey;
    margin: 25px auto;
    text-align: center;
    padding: 10px 0px;
}

.card-form input{
    margin-left: 15px;
    padding: 6px;
}

.card-form button {
    padding: 6px;
    border: none;
    box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.4);
    cursor: pointer;
}

.form-row{
    margin: 8px auto;
}